.root {
  position: relative;
  align-items: center;
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;

  > div:nth-child(2) {
    flex-grow: 1;
  }
}

.map-marker {
  border-radius: 50%;
  cursor: pointer;
}

.map-nav {
  bottom: 32px;
  position: absolute;
  right: 8px;
}

.map-tooltip {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
}

.map-tooltip-field {
  display: flex;
  align-items: center;
}

.map-tooltip-flag {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 18px;
  margin-right: 8px;
  min-width: 28px;
}

.map-tooltip-header {
  color: rgba(255, 255, 255, 1);
  font-size: 14px;
  text-transform: uppercase;
}

.map-tooltip-label {
  color: rgba(255, 255, 255, 0.7);
  font-size: 11px;
  margin-right: 8px;
  text-transform: uppercase;
}

.mapboxgl-popup-content {
  background-color: rgba(0, 0, 0, 0.6) !important;
}

.mapboxgl-popup-close-button {
  color: rgba(255, 255, 255, 0.7);
}

.map-tooltip-value {
  color: rgba(255, 255, 255, 1);
  font-size: 12px;
}

.margin {
  display: flex;
  margin: 4px;
}

.legend {
  align-items: center;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 5px;
  display: flex;
  height: 40px;
  justify-content: space-around;
  position: absolute;
  bottom: 40px;
  width: 350px;
  z-index: 1;
}

.legend-field {
  align-items: center;
  cursor: pointer;
  display: flex;
  justify-content: center;
}

.legend-icon {
  border-color: transparent;
  border-radius: 50%;
  border-style: solid;
  border-width: 2px;
  height: 10px;
  margin-right: 8px;
  width: 10px;
}

.legend-icon-active {
  border-color: rgba(255, 255, 255, 1);
}

.legend-label {
  color: rgba(255, 255, 255, 0.5);
  font-size: 12px;
  text-transform: uppercase;
}

.legend-label-active {
  color: rgba(255, 255, 255, 1);
}